.header {
  height: 230px;
  margin: 0;
  padding: 0;
  position: relative;
}

.header_container {
  height: 230px;
}

.header_icon-main {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #fff;
  visibility: visible;
  margin-top: 40px;
  margin-left: 20px;
}

.header_icon-main:hover, .header_icon-main:focus {
  stroke: #ff6b08;
  transition: all .4s;
}

.header_links-container {
  width: 144px;
  float: right;
  flex-direction: row;
  gap: 40px;
  margin-top: 45px;
  margin-right: 20px;
  display: flex;
}

.header_links {
  color: #fff;
  text-transform: uppercase;
  text-align: right;
  font-family: Roboto;
  font-size: 12px;
}

.header_links:hover, .header_links:focus {
  color: #ff6b08;
  transition: all .4s;
}

.header_current-link:after {
  content: "";
  width: 36px;
  height: 3px;
  cursor: default;
  background-color: #ff001b;
  border-radius: 2px;
  display: block;
  position: absolute;
  top: 60px;
}

.header_main-link {
  visibility: hidden;
}

.header_button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin-top: 60px;
  margin-left: 260px;
  position: relative;
}

.header_button_icon {
  width: 12px;
  height: 12px;
  fill: none;
  stroke: #fff;
  left: 20px;
}

.header_button_icon:hover, .header_button_icon:focus {
  stroke: #ff6b08;
  transition: all .4s;
}

.header_form {
  width: 280px;
  height: 20px;
  margin: auto;
  position: relative;
}

.header_input {
  color: #fff;
  width: 280px;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  margin-top: 60px;
  position: absolute;
}

.header_input:hover, .header_input:focus {
  color: #ff6b08;
  border-bottom: 1px solid #ff6b08;
  outline: none;
  transition: all .4s;
}

.header_error {
  text-align: center;
  color: #ff001b;
  visibility: hidden;
  padding-top: 10px;
  font-family: Roboto;
  font-size: 14px;
}

.header_img {
  height: 230px;
  background-image: url("home_background.e574e51f.jpg");
  background-size: cover;
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .header {
    height: 216px;
    justify-content: space-between;
  }

  .header_icon-main {
    margin-left: 31px;
  }

  .header_icon-main:hover, .header_icon-main:focus {
    stroke: #fff;
    transition: all .4s;
  }

  .header_main-link {
    visibility: visible;
    color: #fff;
    font-family: Roboto;
    font-size: 30px;
    font-weight: 500;
  }

  .header_main-link:hover, .header_main-link:focus {
    color: #ff6b08;
    stroke: #ff6b08;
    transition: all .4s;
  }

  .header_links-container {
    margin-right: 32px;
  }

  .header_form, .header_input {
    width: 360px;
  }

  .header_button {
    margin-left: 340px;
  }

  .header_img {
    height: 216px;
    background-image: url("home_background@2x.72a88096.jpg");
    background-size: cover;
  }
}

@media screen and (min-width: 1280px) {
  .header_icon-main {
    margin-left: 31px;
  }

  .header_icon-main:hover, .header_icon-main:focus {
    stroke: #fff;
    transition: all .4s;
  }

  .header_main-link {
    visibility: visible;
    color: #fff;
    font-family: Roboto;
    font-size: 30px;
    font-weight: 500;
  }

  .header_main-link:hover, .header_main-link:focus {
    color: #ff6b08;
    transition: all .4s;
  }

  .header_links-container {
    margin-right: 32px;
  }

  .header_form, .header_input {
    width: 394px;
  }

  .header_button {
    margin-left: 375px;
  }

  .header_img {
    height: 216px;
    background-image: url("home_background@4x.ae50a285.jpg");
