.movies {
  grid-template-columns: 280px;
  justify-content: center;
  row-gap: 20px;
  padding: 20px 20px 40px;
  display: grid;
}

@media screen and (min-width: 768px) {
  .movies {
    grid-template-columns: 336px 336px;
    gap: 32px;
    padding: 60px 32px;
  }
}

@media screen and (min-width: 1280px) {
  .movies {
    grid-template-columns: 395px 395px 395px;
    column-gap: 16px;
  }
}

.card {
  font-family: Roboto;
  font-weight: 500;
  transition: transform .3s;
}

.card:hover, .card:focus {
  transform: scale(1.02);
}

.card__img {
  width: 280px;
  height: 398px;
  border-radius: 5px;
  margin-bottom: 10px;
}

@media screen and (min-width: 768px) {
  .card__img {
    width: 336px;
    height: 455px;
    margin-bottom: 8px;
  }
}

@media screen and (min-width: 1280px) {
  .card__img {
    width: 395px;
    height: 574px;
    margin-bottom: 12px;
  }
}

.card__info {
  font-size: 12px;
  line-height: 1.33;
}

@media screen and (min-width: 1280px) {
  .card__info {
    font-size: 20px;
    line-height: 1.2;
  }
}

.card__title {
  color: #000;
  text-transform: uppercase;
}

.card__desc {
  color: #ff6b08;
}

.pages {
  justify-content: center;
  gap: 2px;
  margin-bottom: 120px;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .pages {
    margin-bottom: 240px;
  }
}

.pages__button {
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: #000;
  background-color: #0000;
  border: none;
  border-radius: 5px;
  transition: background-color .2s linear, color .2s linear;
}

.pages__button:hover, .pages__button:focus, .pages__button--active {
  color: #fff;
  background-color: #ff6b08;
}

.pages__button--controls {
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pages__icon {
  width: 16px;
  height: 16px;
  fill: none;
  stroke: #000;
}

